import React from 'react';
import img from '../images/about.png';
import img2 from '../images/mission.png';
import { HashLink } from 'react-router-hash-link';

const AboutUs = () => {
    return (
        <>
                <div className="m-auto max-w-6xl p-2 md:p-12 h-5/6" id='about' >

                    <div className="flex flex-col-reverse lg:flex-row py-8 justify-between lg:text-left" data-aos="fade-up">
                        <div className="lg:w-2/5 flex flex-col lg:mx-4 justify-center">
                            <img alt="card img" className="rounded-t float-right lg:mr-20" src={img} />
                        </div>
                        <div className="flex-col justify-center my-4 text-center lg:text-left lg:my-0 lg:justify-end w-full lg:w-3/5 px-8" data-aos="zoom-in" data-aos-delay="500">
                            
                            <h1 className="text-3xl  text-blue-900 font-bold">About Us</h1>
                            <div>
                                <p className='my-3 text-xl text-gray-600 font-semibold'>Welcome to Luckewalllet, where innovation meets convenience in the world of digital finance. We are committed to revolutionizing the way you manage your finances, providing you with a seamless and secure experience at your fingertips.</p>
                            </div>
                            
                            <HashLink smooth to='#who-we-are' className="text-white bg-blue-900 hover:bg-blue-800 inline-flex items-center justify-center w-full px-6 py-2 my-4 text-lg shadow-xl rounded-2xl sm:w-auto sm:mb-0 group">
                                Learn more
                                <svg className="w-4 h-4 ml-1 group-hover: translate-x-2" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor"><path fillRule="evenodd" d="M10.293 3.293a1 1 0 011.414 0l6 6a1 1 0 010 1.414l-6 6a1 1 0 01-1.414-1.414L14.586 11H3a1 1 0 110-2h11.586l-4.293-4.293a1 1 0 010-1.414z" clipRule="evenodd"></path></svg>
                            </HashLink>
                        </div>
                    </div>
                </div>

                <div className="m-auto max-w-6xl p-2 md:p-12 h-5/6" id='mission' >

                    <div className="flex flex-col lg:flex-row py-8 justify-between lg:text-left" data-aos="fade-up">

                        <div className="flex-col my-4 text-center lg:text-left lg:my-0 lg:justify-end w-full lg:w-3/5 px-8" data-aos="zoom-in" data-aos-delay="500">
                            
                            <h1 className="text-3xl  text-blue-900 font-bold">Our Mission</h1>
                            <div>
                                <p className='my-3 text-xl text-gray-600 font-semibold'>At Luckewallet, our mission is to empower individuals and businesses to navigate the digital economy effortlessly. We strive to simplify financial transactions, making them faster, safer, and more accessible for everyone.</p>
                            </div>
                        </div>
                        <div className='lg-w-1/5'></div>
                        <div className="lg:w-1/5 flex lg:mx-4">
                            <img alt="card img" className="rounded-t float-right lg:m-auto lg:ml-30" src={img2} />
                        </div>
                    </div>
                </div>
        </>
    )
}

export default AboutUs;